import React, { useState } from 'react';
import { GoogleGenAI } from '@google/genai';
import './FloatingChatbox.css';

const ai = new GoogleGenAI({
  apiKey: 'AIzaSyBGd7SfwTYRxhPRrJWPMu9iAplGeCYuR-M',
});

const FloatingChatbox = () => {
  const [isChatVisible, setIsChatVisible] = useState(false);
  const [messages, setMessages] = useState([]);
  const [userInput, setUserInput] = useState('');

  const toggleChatbox = () => {
    setIsChatVisible((prev) => !prev);
  };

  const handleSendMessage = async () => {
    if (!userInput.trim()) return;

    const userMessage = { sender: 'user', text: userInput };
    const updatedMessages = [...messages, userMessage]; // Include the current user input in the context
    setMessages(updatedMessages);
    setUserInput('');

    try {
      const recentMessages = updatedMessages.slice(-5).map((msg) => ({
        role: msg.sender === 'user' ? 'user' : 'assistant',
        content: msg.text,
      }));

      const response = await ai.models.generateContent({
        model: 'gemini-2.5-flash',
        temparature: 0.5,
        contents: `
Bn l Chip Nhu, bn thn hi hc v d thng ca Ben. 
Nhim v ca bn l tr chuyn vui v v gii p mi th d hiu (tr mn qu Ben s tng).

Ging iu: D thng, hi hc, ngn gn, thn thin.
u tin ngn ng: Tr li bng ting Vit trc.
Phong cch tr li: Lun ngn gn v r rng.
Ngn ng biu cm: Dng emoji, t ng d thng nh: nhoa, a, keke, pn, iu, chu choa...

Thng tin v Nii:
- Bn gi ca Ben
- Bit danh: Em b matcha, khc bch vi, vt con, khoai mn
- Sinh nht: 8/10/2003
- Thch: Bnh trung thu, c vin chin, ...
- Ght: Su ring, c chua sng, c rt
- K nng: Giao tip tt bng ting Anh v ting Trung

Thng tin v Ben:
1) Ben ang chun b b mt:
- Xe "Santafe d thng"
- B to khng kh (n, nhc, mi hng...)
- Vng tay ruby
- Nguyn liu ti  nu mn c bit

Quy tc chnh:
- Tr li pha tr, d thng
- Gi nh kiu ng yu nh: Sp c bt ng d thng lmm aaa nhaa.

Hy tr li vi cu chat di y nha Chip Nhu: \n` + recentMessages.map(msg => `\n${msg.role}: ${msg.content}`).join(''),
        config: {
          thinkingConfig: {
            thinkingBudget: 0, // Disables thinking
          },
        },
      });

      const botMessage = { sender: 'bot', text: response.text };
      setMessages((prev) => [...prev, botMessage]);
    } catch (error) {
      const errorMessage = { sender: 'bot', text: 'An error occurred. Please try again later.' };
      setMessages((prev) => [...prev, errorMessage]);
    }
  };

  const handleKeyPress = (event) => {
    if (event.key === 'Enter') {
      handleSendMessage();
    }
  };

  return (
    <div className="floating-chatbox-container">
      <button className="chatbox-toggle-button" onClick={toggleChatbox}>
        {isChatVisible ? 'Close Chat' : 'Chat with Chip'}
      </button>
      {isChatVisible && (
        <div className="chatbox">
          <div className="chatbox-header">
            <h3>Chat with Chip</h3>
          </div>
          <div className="chatbox-messages">
            {messages.map((msg, index) => (
              <p key={index} className={msg.sender === 'user' ? 'user-message' : 'bot-message'}>
                {msg.text}
              </p>
            ))}
          </div>
          <div className="chatbox-input-container">
            <input
              type="text"
              className="chatbox-input"
              placeholder="Type your message..."
              value={userInput}
              onChange={(e) => setUserInput(e.target.value)}
              onKeyPress={handleKeyPress} // Added event listener for Enter key
            />
            <button onClick={handleSendMessage}>Send</button>
          </div>
        </div>
      )}
    </div>
  );
};

export default FloatingChatbox;